.Pagination {
  list-style: none;
  padding: 0;
  margin: 0;
  margin: 20px 0;
  border-radius: 4px;
  text-align: right;
}

.Pagination li {
  display: inline-block;
}

.Pagination > li > a,
.Pagination > li > span {
  text-decoration: none;
  color: #000;
  padding: 6px 12px;
  border: 1px solid #ddd;
  margin-left: -1px;
  display: block;
  font-size: 0.875em;
  line-height: 1.42857143;
  transition: 0.2s ease-in-out;
}

.Pagination .active > a {
  background-color: #000;
  color: #fff;
  border-color: #000;
  font-weight: 600;
}