.LoadingIcon {
  padding: 20px 0;
  width: 100% !important;
}

.LoadingIcon::after {
  content: "";
  display: block;
  height: 30px;
  width: 30px;
  color: rgb(160, 160, 160);
  position: relative;
  border: 8px solid;
  border-radius: 50%;
  border-right-color: #fff;
  animation: rotate 1s linear infinite;
  /* background-color: #1a1a1a; */
  margin-left: auto;
  margin-right: auto;
}

@keyframes rotate {
  0% { transform: rotate(0); }
  100% { transform: rotate(360deg); }
}