.email_form {
  margin-top: 20px;

}
.email_form__fields {
  display: flex;
  margin-bottom: 10px;
}

.email_form__email {
  border: 1px solid #979797 !important;
  border-radius: 0  !important;
  font-size: 16px !important;
  height: 100% !important;
  height: 38px !important;
  width: 100% !important;
  font-family: Arial, Helvetica, sans-serif !important;
  margin-bottom: 10px;
}

.email_form__submit {
  background-color: #000;
  cursor: pointer;
  box-shadow: none;
  border: 1px solid #000;
  width: 38px;
  height: 38px;
  position: relative;
  transition: background-color 0.2s ease-in-out;
  color: #fff;
  font-weight: 600;
}

.email_form__submit:hover {
  background-color: #444;
}

.email_form__checkboxlabel {
  font-size: 12px;
  margin-left: 4px;
  display: inline-block;
  font-weight: 400;
}