.GallerySliderWrapper {
  height: 260px;
  margin-bottom: 30px;
}

/* .GallerySliderWrapper__slide {
  width: auto !important;
} */

.GallerySliderWrapper__image,
.GallerySliderWrapper__slide,
.GallerySliderWrapper__slick {
  height: 260px;
  display: block;
  overflow: hidden;
}

.GallerySliderWrapper__image {
  margin-right: 10px;
  cursor: zoom-in;
}

.GallerySliderWrapper[data-bp~=medium] {
  height: 320px;
}

.GallerySliderWrapper[data-bp~=medium] .GallerySliderWrapper__image,
.GallerySliderWrapper[data-bp~=medium] .GallerySliderWrapper__slide,
.GallerySliderWrapper[data-bp~=medium] .GallerySliderWrapper__slick{
  height: 320px;
}

.GallerySliderWrapper[data-bp~=large] {
  height: 420px;
}

.GallerySliderWrapper[data-bp~=large] .GallerySliderWrapper__image,
.GallerySliderWrapper[data-bp~=large] .GallerySliderWrapper__slide,
.GallerySliderWrapper[data-bp~=large] .GallerySliderWrapper__slick{
  height: 420px;
}

.GallerySliderWrapper[data-bp~=xlarge] {
  height: 520px;
}

.GallerySliderWrapper[data-bp~=xlarge] .GallerySliderWrapper__image,
.GallerySliderWrapper[data-bp~=xlarge] .GallerySliderWrapper__slide,
.GallerySliderWrapper[data-bp~=xlarge] .GallerySliderWrapper__slick{
  height: 520px;
}