.app {
  box-sizing: border-box;
  font-size: 16px;
  color: #1a1a1a;
  /* overflow-x: hidden; */
  overflow: hidden;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.app * {
  /* Some minimal reset stuff */
  box-sizing: border-box;
  font-family: Helvetica, Arial sans-serif;
}

.app h1,
.app h2,
.app h3,
.app h4 {
  font-weight: 600;
  line-height: 1.15;
}

.landscapevideo {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  width: 100%;
}

.landscapevideo iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.atAppBtnWhite {
  cursor: pointer;
  background-color: #fff;
  color: #1a1a1a;
  appearance: none;
  border-radius: 0;
  padding: 12px 30px;
  font-weight: 600;
  font-size: 0.875em;
  outline: 0;
  display: block;
  margin: 0;
  margin-top: 20px;
  width: 100%;
  border: 0;
  transition: background-color 0.2s ease-in-out;
  text-transform: uppercase;
}

.atAppBtnWhite:hover {
  background-color: #ddd;
}

input[type="text"].atFormInput,
input[type="email"].atFormInput,
textarea.atFormInput {
  background: #fff;
  display: block;
  border: 0;
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
  width: 100%;
  margin-top: 20px;
  padding: 12px;
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 4px;
}

input[type="text"].atFormInput_error,
input[type="email"].atFormInput_error {
  border: 1px solid #ec4747;
}

.atTextArea {
  height: 300px;
}