.videocarouselswiper {
  margin-right: -5px !important;
  margin-left: -5px !important;
}

.videocarouselswiper__slide {
  padding: 0 5px;
}

.videocarouselswiper__slide__inner {
  position: relative;
  cursor: pointer;
}

.videocarouselswiper__slide__text {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  padding: 10px;
  color: #fff;
}

.videocarouselswiper__slide__text p {
  font-size: 12px;
  margin-bottom: 0;
}

.videocarouselswiper__slide__text h4 {
  font-size: 16px;
  margin-bottom: 4px;
  margin-top: 0;
}
