.projectheader h2 {
  font-size: 1.625em;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 0.6em;
}

.projectheader p,
.projectheader ul,
.projectheader ol {
  font-size: 1em;
  line-height: 1.4;
  margin-top: 0;
  margin-bottom: 1em;
}

@media all and (min-width: 768px) {
  .projectheader p,
  .projectheader ul,
  .projectheader ol {
    font-size: 1.125em;
  }
}

@media all and (min-width: 1024px) {
  .projectheader p,
  .projectheader ul,
  .projectheader ol {
    font-size: 1.25em;
  }

  .projectheader h2 {
    font-size: 2em;
  }
}

.projectheader a {
  color: #1a1a1a;
  text-decoration: underline;
}
