.Filterwrap {
  background-color: #1a1a1a;
  margin-bottom: 20px;
}

.Filter {
  color: #fff;
  position: relative;
  background-color: #1a1a1a;
}

@media all and (max-width: 767px) {
  .Filter {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow-y: scroll;
    z-index: 2000;
    transform: translateX(-100%);
    transition: transform 0.2s ease-in-out;
    margin-bottom: 0;
  }

  .Filter_isopen {
    transform: translateX(0);
  }
}

.Filter__inner {
  background-color: #242424;
}

.Filter__inner_isopen {
  display: block;
}

.Filter__tab {
  display: none;
  padding: 20px;
}

@media all and (max-width: 767px) {
  .Filter__tab {
    padding-top: 0;
  }
}

.Filter__tab_isopen {
  display: block;
}

.Filter__inner h3 {
  margin-top: 30px;
  padding-top: 20px;
  font-size: 1em;
  color: #fff;
}

.Filter__toggle {
  background-color: #464545;
  cursor: pointer;
  appearance: none;
  border: 0;
  box-shadow: 0;
  color: #fff;
  padding: 20px;
  display: inline-block;
  font-size: 1em;
  outline: 0;
  margin: 0;
}

.Filter__toggle:nth-of-type(4) {
  border-right: 0;
}

@media all and (min-width: 768px) {
  .Filter__toggle {
    font-size: 1em;
    border-right: 1px solid #fff;
  }
}

.Filter__toggle i {
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='11' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.197 0L7 1.833 3.5 5.5 7 9.167 5.197 11 0 5.5z' fill='%23FFF' fill-rule='nonzero'/%3E%3C/svg%3E");
  display: inline-block;
  width: 7px;
  height: 11px;
  margin-left: 10px;
  vertical-align: middle;
  transform: rotate(-90deg);
  transition: 0.2s ease;
}

.Filter__toggle_isactive {
  background-color: #242424;
}

.Filter__toggle_isactive i {
  transform: rotate(90deg);
}

@media all and (min-width: 500px) {
  .Filter_section__checkboxes {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

@media all and (min-width: 768px) {
  .Filter__submit {
    margin-left: auto;
    width: 200px;
  }
}

.Filter__clearbtn {
  cursor: pointer;
  background-color: #3d3d3d;
  color: #fff;
  appearance: none;
  border-radius: 0;
  padding: 16px 20px;
  font-weight: 600;
  margin: 0;
  font-size: 0.75em;
  outline: 0;
  display: inline-block;
  border: 0;
  transition: background-color 0.2s ease-in-out;
}

.FilterBtns_wrap {
  display: none;
}

.MobileFilterBtn {
  width: 100%;
  text-align: left;
  position: relative;
  font-weight: bold;
  border-top: 2px solid #1a1a1a;
}

.MobileFilterBtn i {
  position: absolute;
  right: 20px;
}

.Filter__inner_header button {
  cursor: pointer;
  background: #333;
  color: #fff;
  box-shadow: none;
  border: 0;
  text-transform: uppercase;
  font-weight: bold;
  padding: 20px;
}

.Filter__inner_header .FilterBtns__title {
  padding: 20px;
  font-weight: 600;
}

.FilterToggle {
  color: #fff;
  background-color: #242424;
  box-shadow: 0;
  border: 0;
  padding: 16px 20px;
  font-weight: bold;
  text-transform: uppercase;
}

.FilterToggle i {
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='11' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.197 0L7 1.833 3.5 5.5 7 9.167 5.197 11 0 5.5z' fill='%23FFF' fill-rule='nonzero'/%3E%3C/svg%3E");
  display: inline-block;
  width: 7px;
  height: 11px;
  vertical-align: middle;
  margin-right: 6px;
  margin-top: -1px;
}

@media all and (min-width: 768px) {
  .FilterBtns_wrap {
    display: flex;
    align-items: stretch;
  }

  .FilterBtns__title {
    display: flex;
    align-items: center;
    padding: 0 20px;
    background: #000;
    font-weight: bold;
  }

  .FilterToggle {
    display: none;
  }

  .MobileFilterBtn {
    display: none;
  }

  .Filter_mob_header {
    display: none;
  }

  .Filter__inner_header {
    display: none;
  }
}

input[type="text"].Filter__name_input {
  width: 100%;
  box-shadow: 0;
  border: 0;
  border-radius: 2px;
  font-size: 16px;
  padding: 18px 20px;
  margin-bottom: -20px;
  box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
  background: #fff;
  display: block;
  border: 0;
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
  width: 100%;
}
