.socials {
  display: flex;
  margin-top: 20px;
}

.socials__icon {
  display: block;
  text-indent: -9999px;
  width: 30px;
  height: 30px;
  background-size: cover;
  margin-right: 10px;
  transition: opacity 0.2s ease;
  cursor: pointer;
  border: 0;
  background: transparent;
}

.socials__icon:hover {
  opacity: 0.8;
}

