.projectpreview {
  margin-bottom: 40px;
}

.projectpreview__imagelink {
  display: block;
  height: 0;
  padding-bottom: 74.7619047619%;
  overflow-y: hidden;
  position: relative;
}

.projectpreview__text {
  padding: 15px 10px;
  flex: 1 0 auto;
}

.projectpreview__text h3 {
  font-size: 1.25em;
  margin-bottom: 0;
  margin-top: 0.2em;
  color: #1a1a1a;
}

.projectpreview__text__p {
  margin: 0;
  margin-bottom: 1em;
  padding: 0;
  font-weight: 200;
  font-size: 1.25em;
  color: #1a1a1a;
}

.projectpreview__meta {
  padding: 0 10px;
}

.projectpreview__imagelink img {
  display: block;
  width: 100% !important;
  height: auto !important;
}

.projectpreview__winner {
  background-color: rgba(0, 0 ,0 ,0.8);
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  font-weight: 700;
  padding: 5px 10px;
  min-height: 60px;
  display: flex;
  font-size: 16px;
  align-items: center;
}