.projectmeta {
  border-top: 1px solid #393939;
  border-bottom: 1px solid #393939;
  padding: 10px 0;
  color: #555;
}

.projectmeta p {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-size: 0.75em;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  padding-right: 10px;
}

.projectmeta p::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 10px;
  background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
}

.projectmeta_single p {
  overflow: inherit;
  white-space: inherit;
}
