.modalcarousel__content {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  outline: 0;
  color: #fff;
  display: flex;
  align-items: center;
}

.modalcarousel__overlay {
  position: fixed;
  z-index: 2000000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}

.modalcarousel__close {
  cursor: pointer;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 2;
  border: 0;
  background: transparent;
  width: 40px;
  height: 40px;
  text-indent: 9999px;
}

.modalcarousel__close::after,
.modalcarousel__close::before {
  content: "";
  display: block;
  background: #fff;
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -1px;
}

.modalcarousel__close::after {
  transform: rotate(45deg);
}

.modalcarousel__close::before {
  transform: rotate(-45deg);
}

.modalcarousel__slidecount {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 14px;
}

.modalcarousel__player {
  margin-left: auto;
  margin-right: auto;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
}

.modalcarousel__player_portrait {
  margin-left: auto;
  margin-right: auto;
  height: 0;
  padding-bottom: 177.7777777778%;
  position: relative;
  max-width: 375px;
}

@media all and (min-width: 768px) {
  .modalcarousel__player {
    width: calc(100% - 100px);
  }
}

.modalcarousel__player iframe,
.modalcarousel__player_portrait iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ReactModalPortal .swiper-button-next,
.ReactModalPortal .swiper-button-prev {
  background: black !important;
  height: 40px;
  width: 40px;
}

.videocarouselswiper__slide__inner {
  width: 100vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}