.at-app-global .slick-next,
.at-app-global .slick-prev {
  background-color: black;
  text-indent: -9999px;
  border: 0;
  cursor: pointer;
  border-radius: none;
  height: 40px;
  width: 40px;
  display: block;
  z-index: 2;
  outline: none;
  bottom: 0;
  position: absolute;
  transition: background 0.2s ease-in-out;
  transform: inherit;
}

.at-app-global .slick-next:hover,
.at-app-global .slick-prev:hover {
  background-color: #444;
}

.at-app-global .slick-next {
  transform: rotate(180deg);
}

.at-app-global .slick-next::after,
.at-app-global .slick-prev::after,
.at-app-global .slick-next::before,
.at-app-global .slick-prev::before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 3px;
  background-color: #fff;
  margin-left: -7px;
  margin-top: -2px;
  transform-origin: center center;
}

.at-app-global .slick-next::after,
.at-app-global .slick-prev::after {
  transform: translateY(-3px) rotate(-45deg);
}

.at-app-global .slick-next::before,
.at-app-global .slick-prev::before {
  transform: translateY(3px) rotate(45deg);
}

.at-app-global .slick-next {
  right: 0;
}

.at-app-global .slick-prev {
  left: 0;
}

.at-app-global .slick-disabled {
  opacity: 0;
}

.at-app-global .slick-slide * {
  outline: none;
}

.at-app-global .slick-slider {
  opacity: 0;
  transition: opacity 0.4s ease;
}

.at-app-global .slick-slider.slick-fadein {
  opacity: 1;
}

.at-app-global .cap-btns button {
  text-transform: uppercase !important;
  letter-spacing: -0.02em;
}

.at-app-btn {
  color: #fff;
  line-height: 1.4;
  border: 0;
  appearance: none;
  box-shadow: 0;
  background: #000;
  padding: 15px 20px;
  font-weight: 600;
  cursor: pointer;
  display: block;
  text-align: center;
  width: 100%;
  transition: background-color 0.2s ease;
  outline: none;
}

.at-app-btn:hover {
  background-color: #444;
}

.at-app-btn--red {
  background-color: #e91515;
}
