.CourseLinkWrapper {
  position: relative;
  margin-top: 20px;
}

.CourseLinkWrapper>div {
  margin-bottom: 40px;
}

.CourseLinkWrapper[data-bp~=medium],
.CourseLinkWrapper[data-bp~=large],
.CourseLinkWrapper[data-bp~=xlarge],
.CourseLinkWrapper[data-bp~=xxlarge] {
  display: flex;
  margin-left: -10px;
  margin-right: -10px;
  flex-wrap: wrap;
  margin-top: 40px;
}

.CourseLinkWrapper[data-bp~=medium]>div,
.CourseLinkWrapper[data-bp~=large]>div,
.CourseLinkWrapper[data-bp~=xlarge]>div,
.CourseLinkWrapper[data-bp~=xxlarge]>div {
  padding: 0 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.CourseLinkWrapper[data-bp~=large]>div {
  width: 33.3333333333%;
}

.CourseLinkWrapper[data-bp~=xlarge]>div {
  width: 25%;
}

.CourseLinkWrapper[data-bp~=xxlarge]>div {
  width: 20%;
}