.at-app-img {
  display: block;
  width: 100%;
  height: auto;
}

.at-app-mt-40 {
  margin-top: 40px;
}

.at-app-global .swiper-button-next,
.at-app-global .swiper-button-prev,
.ReactModalPortal .swiper-button-next,
.ReactModalPortal .swiper-button-prev {
  background: black;
  height: 40px;
  width: 40px;
}

.at-app-global .swiper-button-next,
.ReactModalPortal .swiper-button-next {
  transform: rotate(180deg);
}

.at-app-global .swiper-button-next:after,
.at-app-global .swiper-button-prev:after,
.ReactModalPortal .swiper-button-next:after,
.ReactModalPortal .swiper-button-prev:after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 3px;
  background-color: #fff;
  margin-left: -7px;
  margin-top: -2px;
  transform-origin: center center;
  transform: translateY(3px) rotate(45deg);
}

.at-app-global .swiper-button-next:before,
.at-app-global .swiper-button-prev:before,
.ReactModalPortal .swiper-button-next:before,
.ReactModalPortal .swiper-button-prev:before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 3px;
  background-color: #fff;
  margin-left: -7px;
  margin-top: -2px;
  transform-origin: center center;
  transform: translateY(-3px) rotate(-45deg);
}

.at-app-global .swiper-pagination {
  font-size: 12px;
}

.ReactModalPortal .swiper-button-next {
  right: 0;
}

.ReactModalPortal .swiper-button-prev {
  left: 0;
}

.at-app-global .fullwidth-video-swiper {
  padding-bottom: 50px;
}

.at-app-global .fullwidth-video-swiper .swiper-button-next {
  bottom: 0;
  right: 0;
  top: inherit;
}

.at-app-global .fullwidth-video-swiper .swiper-button-prev {
  bottom: 0;
  right: 50px;
  left: inherit;
  top: inherit;
}

.at-app-global .CmVideoCarousel {
  overflow: hidden;
}

.at-app-global .CmVideoCarousel .swiper-button-prev,
.at-app-global .CmVideoCarousel .swiper-button-next {
  top: 0;
  margin-top: 0;
}

.at-app-global .CmVideoCarousel .swiper-button-prev {
  left: 5px;
}

.at-app-global .CmVideoCarousel .swiper-button-next {
  right: 5px;
}

.at-app-global .CmVideoCarousel .swiper-pagination {
  text-align: right;
  padding-right: 10px;
  bottom: 0;
}

.at-app-global .CmVideoCarousel .swiper-container {
  padding-bottom: 20px;
}

.at-app-global .CmVideoCarousel .swiper-wrapper {
  height: auto;
}

.at-form {
  width: 100%;
}

/* Forms */
.input-error {
  color: #fff !important;
  background-color: #ec4747 !important;
  padding: 3px;
  display: inline-block;
  font-size: 12px;
  margin-top: 6px;
}
