.Projects__projects {
  position: relative;
}

.Projects__projects[data-bp~=medium],
.Projects__projects[data-bp~=large],
.Projects__projects[data-bp~=xlarge],
.Projects__projects[data-bp~=xxlarge] {
  display: flex;
  margin-left: -10px;
  margin-right: -10px;
  flex-wrap: wrap;
}

.Projects__projects[data-bp~=medium]>div,
.Projects__projects[data-bp~=large]>div,
.Projects__projects[data-bp~=xlarge]>div,
.Projects__projects[data-bp~=xxlarge]>div {
  padding: 0 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.Projects__projects[data-bp~=large]>div {
  width: 33.3333333333%;
}

.Projects__projects[data-bp~=xlarge]>div {
  width: 25%;
}

.Projects__projects[data-bp~=xxlarge]>div {
  width: 20%;
}
