.Project__content p,
.Project__content ul,
.Project__content ol {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 0.9375em;
  line-height: 1.4;
  margin-bottom: 1.5em;
  margin-top: 0;
  max-width: 760px;
}

.Project__content a {
  color: #1a1a1a;
  text-decoration: underline;
}

.Project__title {
  font-size: 1.625em;
  margin: 0 0 0.2em;
  color: #1a1a1a;
}

.Project__title__sub {
  font-size: 1.625em;
  font-weight: 200;
  margin-top: 0;
}

.Project__flex_col1 {
 margin-bottom: 20px;
}

.Project[data-bp~=medium] .Project__flex {
  display: flex;
}

.Project[data-bp~=medium] .Project__flex_col1 {
  width: 320px;
  padding-right: 40px;
}

.Project[data-bp~=medium] .Project__flex_col2 {
  width: calc(100% - 360px);
  max-width: 760px;
}
