.CourseLink {
  color: #1a1a1a;
  text-decoration: none;
  display: block;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.CourseLink__media {
  height: 0;
  padding-bottom: 44%;
  position: relative;
  background-color: #f7f7f7;
  transition: 0.4s ease;
}

.CourseLink:hover .CourseLink__media,
.CourseLink:hover .CourseLink__text {
  opacity: 0.8;
}


.CourseLink__media span {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.CourseLink__media img {
  object-fit: cover;
  object-position: center center;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: block;
}

.CourseLink__text {
  z-index: 2;
  position: relative;
  padding: 15px 10px 25px;
  flex: 1 0 auto;
  color: #1a1a1a;
  transition: 0.4s ease;
  border-bottom: 1px solid #343434;
}

.CourseLink__text h3 {
  margin: 0;
  margin-bottom: 0em;
  margin-top: 0.2em;
  font-size: 1.25em;
  line-height: 1.15;
}

.CourseLink__text p {
  margin: 0;
  margin-bottom: 1em;
  padding: 0;
  font-weight: 200;
  font-size: 1.25em;
}