.Projects__back {
  appearance: none;
  border: 0;
  border-radius: 0;
  cursor: pointer;
  font-size: 0.825em;
  font-weight: 600;
  display: inline-block;
  margin-top: 10px;
  margin-bottom: 20px;
  outline: 0;
  color: #1a1a1a;
  transition: color 0.2s ease;
  background: transparent;
}

.Projects__back:hover {
  color: #666;
}

.Projects__back i {
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='11' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.197 0L7 1.833 3.5 5.5 7 9.167 5.197 11 0 5.5z' fill='%231A1A1A' fill-rule='nonzero'/%3E%3C/svg%3E");
  display: inline-block;
  width: 7px;
  height: 11px;
  margin-right: 6px;
  vertical-align: middle;
}
