.BackButton {
  background-color: #1a1a1a;
  color: #fff;
  margin-bottom: 10px;
}

.BackButton__link {
  display: inline-block;
  background-color: #242424;
  color: #fff;
  font-weight: 600;
  font-size: 0.75em;
  text-decoration: none;
  padding: 18px;
  border: 0;
  outline: 0;
  appearance: none;
  transition: background-color 0.2s ease-in-out;
  margin: 0;
}

.BackButton__link i {
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='11' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.197 0L7 1.833 3.5 5.5 7 9.167 5.197 11 0 5.5z' fill='%23FFF' fill-rule='nonzero'/%3E%3C/svg%3E");
  display: inline-block;
  width: 7px;
  height: 11px;
  margin-right: 10px;
  vertical-align: middle;
}

.BackButton__link:hover {
  background-color: #444;
}